
Template.__checkName("status_semantic_ui");
Template["status_semantic_ui"] = new Template("Template.status_semantic_ui", (function() {
  var view = this;
  return Blaze.Unless(function() {
    return Spacebars.call(view.lookup("connected"));
  }, function() {
    return [ "\n    ", HTML.DIV({
      "class": function() {
        return [ "ui ", Spacebars.mustache(view.lookup("option"), "classes"), " message" ];
      }
    }, "\n      ", HTML.DIV({
      "class": "content"
    }, "\n        ", HTML.DIV({
      "class": "header"
    }, "\n          ", Blaze.View("lookup:message", function() {
      return Spacebars.mustache(view.lookup("message"));
    }), " ", HTML.CharRef({
      html: "&nbsp;",
      str: "Â "
    }), "\n          ", Blaze.View("lookup:extraMessage", function() {
      return Spacebars.mustache(view.lookup("extraMessage"));
    }), "\n          ", Blaze.If(function() {
      return Spacebars.call(view.lookup("showReconnect"));
    }, function() {
      return [ "\n            ", HTML.A({
        href: "#",
        "class": "alert-link"
      }, Blaze.View("lookup:reconnectLabel", function() {
        return Spacebars.mustache(view.lookup("reconnectLabel"));
      })), "\n          " ];
    }), "\n        "), "\n      "), "\n    "), "\n  " ];
  });
}));
