
Template.__checkName("status_bootstrap3");
Template["status_bootstrap3"] = new Template("Template.status_bootstrap3", (function() {
  var view = this;
  return Blaze.Unless(function() {
    return Spacebars.call(view.lookup("connected"));
  }, function() {
    return [ "\n    ", HTML.DIV({
      "class": function() {
        return [ "alert ", Spacebars.mustache(view.lookup("option"), "classes"), " text-center" ];
      },
      role: "alert"
    }, "\n      ", HTML.STRONG("\n        ", HTML.SPAN({
      "class": "glyphicon glyphicon-warning-sign"
    }), "\n        ", Blaze.View("lookup:message", function() {
      return Spacebars.mustache(view.lookup("message"));
    }), "\n      "), "\n      ", Blaze.View("lookup:extraMessage", function() {
      return Spacebars.mustache(view.lookup("extraMessage"));
    }), "\n\n      ", Blaze.If(function() {
      return Spacebars.call(view.lookup("showReconnect"));
    }, function() {
      return [ "\n        ", HTML.A({
        href: "#",
        "class": "alert-link"
      }, Blaze.View("lookup:reconnectLabel", function() {
        return Spacebars.mustache(view.lookup("reconnectLabel"));
      })), "\n      " ];
    }), "\n    "), "\n  " ];
  });
}));
