
Template.__checkName("status_materialize");
Template["status_materialize"] = new Template("Template.status_materialize", (function() {
  var view = this;
  return Blaze.Unless(function() {
    return Spacebars.call(view.lookup("connected"));
  }, function() {
    return [ "\n    ", HTML.DIV({
      "class": function() {
        return [ "center card-panel orange lighten-5 ", Spacebars.mustache(view.lookup("classes")) ];
      }
    }, "\n      ", HTML.SPAN({
      "class": "lime-text text-darken-4"
    }, "\n      ", HTML.STRONG("\n        ", HTML.I({
      "class": "small mdi-alert-warning"
    }), "\n      "), "\n        ", Blaze.View("lookup:message", function() {
      return Spacebars.mustache(view.lookup("message"));
    }), "\n      "), "\n      ", Blaze.View("lookup:extraMessage", function() {
      return Spacebars.mustache(view.lookup("extraMessage"));
    }), "\n      ", Blaze.If(function() {
      return Spacebars.call(view.lookup("showReconnect"));
    }, function() {
      return [ "\n        ", HTML.A({
        href: "#",
        "class": "alert-link"
      }, Blaze.View("lookup:reconnectLabel", function() {
        return Spacebars.mustache(view.lookup("reconnectLabel"));
      })), "\n      " ];
    }), "\n    "), "\n  " ];
  });
}));
